import { Component, OnInit } from '@angular/core';
import Chart from 'chart.js';
import { UsuarioGuardadoService } from "../../servicios/usuarioguardado.service";
import { TareasService } from "../../servicios/tareas.service";
import { ActividadesRealizadas } from "../../models/actividadesRealizadas";
import { UsuariosService } from '../../servicios/usuarios.service';
import { Router } from '@angular/router';

declare interface TableData {
    headerRow: string[];
    dataRows: string[][];
}

@Component({
    selector: 'dashboard-cmp',
    moduleId: module.id,
    templateUrl: 'dashboard.component.html'
})

export class DashboardComponent implements OnInit{

  public canvas : any;
  public ctx;
  public chartColor;
  public chartEmail;
  public chartHours;


  public tableData1: TableData;
  public tableData2: TableData;
  public tableData3: TableData;
  public listaTareasAsignadasPropias: ActividadesRealizadas[];
  public listaTareasNoAsignadas: ActividadesRealizadas[];
  public dataRows: Array<Array<string>>= [];
  public dataLine: Array<string> = [];
  public tareasSeleccionada: ActividadesRealizadas;
  public horas: string;
  public valoracion: string;
  public tSolicitadas: number;
  public tRealizadas: number;
  constructor(
    private router: Router,
    public usuarioService: UsuariosService,
    public usuarioGuardado:UsuarioGuardadoService,
    public tareasService:TareasService
     ) { }

    ngOnInit(){
//        console.log("DashboardComponent.ngOnInit()");
        this.horas =this.usuarioGuardado.getBolsaHora();
        this.valoracion =this.usuarioGuardado.getReputacion();

        this.tareasService.solicitadasPorUsuario(this.usuarioGuardado.getToken(),this.usuarioGuardado.getUsuarioId()).subscribe( data => {
          this.listaTareasNoAsignadas = data.data;
          this.tSolicitadas = this.listaTareasNoAsignadas.length;
        });

        this.tareasService.realizadasPorUsuario(this.usuarioGuardado.getToken(),this.usuarioGuardado.getUsuarioId()).subscribe( data => {
              this.listaTareasAsignadasPropias = data.data;
              this.tRealizadas = this.listaTareasAsignadasPropias.length;
        });



        // Esta lista de tareas asignadas a mi sin terminar
        this.tareasService.actividadesEnRealizacion(this.usuarioGuardado.getToken(),this.usuarioGuardado.getUsuarioId()).subscribe( data => {
        this.listaTareasNoAsignadas = data.data;
//        console.log("DashboardComponent.ngOnInit() data.data= "+  JSON.stringify(data.data) );

        this.dataRows= [];

        for (var tarea of this.listaTareasNoAsignadas) {

            this.dataLine = [];
            this.dataLine.push(String(tarea.id));
            this.dataLine.push(tarea.habilidad.descripcion);

            this.dataLine.push(tarea.usuario_solicita.nombre.concat(
                                ' ',
                                tarea.usuario_solicita.apellido1,
                                ' ',
                                tarea.usuario_solicita.apellido2)
            );

            this.dataLine.push(tarea.observacion);
            this.dataLine.push(tarea.habilidad.horasEstipuladas);
             this.dataRows.push(this.dataLine);
        }

        this.tableData1 = {
            headerRow: [   'Actividad', 'Solicita', 'Observacion', 'Horas Estipuladas', 'Terminar'],
            dataRows: this.dataRows
        };

    });

        // Esta lista de tareas solicitadas
        this.tareasService.actividadesEnSolicitud(this.usuarioGuardado.getToken(),this.usuarioGuardado.getUsuarioId()).subscribe( data => {
        this.listaTareasNoAsignadas = data.data;
        this.dataRows= [];
        for (var tarea of this.listaTareasNoAsignadas) {
            this.dataLine = [];
            this.dataLine.push(String(tarea.id));
            this.dataLine.push(tarea.habilidad.descripcion);
            this.dataLine.push(tarea.usuario_solicita.nombre.concat(
                                ' ',
                                tarea.usuario_solicita.apellido1,
                                ' ',
                                tarea.usuario_solicita.apellido2)
            );
            this.dataLine.push(tarea.observacion);
            this.dataLine.push(tarea.habilidad.horasEstipuladas);
            this.dataRows.push(this.dataLine);
        }
        this.tableData2 = {
            headerRow: [  'Actividad', 'Solicita', 'Observacion', 'Horas Estipuladas', 'Aceptar'],
            dataRows: this.dataRows
        };
    });

        // Esta lista de tareas terminadas realizadas por mi o solicitadas por mi
        this.tareasService.actividadesEnTerminadas(this.usuarioGuardado.getToken(),this.usuarioGuardado.getUsuarioId()).subscribe( data => {
        this.listaTareasNoAsignadas = data.data;
        this.dataRows= [];

        for (var tarea of this.listaTareasNoAsignadas) {

            this.dataLine = [];
            this.dataLine.push(String(tarea.id));
            this.dataLine.push(tarea.habilidad.descripcion);

            this.dataLine.push(tarea.usuario_solicita.nombre.concat(
                                ' ',
                                tarea.usuario_solicita.apellido1,
                                ' ',
                                tarea.usuario_solicita.apellido2)
            );

            this.dataLine.push(tarea.observacion);
            this.dataLine.push(tarea.habilidad.horasEstipuladas);
            this.dataRows.push(this.dataLine);
        }

        this.tableData3 = {
            headerRow: [   'Actividad', 'Solicita', 'Observacion', 'Horas Estipuladas', 'Consultar'],
            dataRows: this.dataRows
        };

    });
      this.chartColor = "#FFFFFF";

      this.canvas = document.getElementById("chartHours");
      this.ctx = this.canvas.getContext("2d");

      this.chartHours = new Chart(this.ctx, {
        type: 'line',

        data: {
          labels: ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct"],
          datasets: [{
              borderColor: "#6bd098",
              backgroundColor: "#6bd098",
              pointRadius: 0,
              pointHoverRadius: 0,
              borderWidth: 3,
              data: [300, 310, 316, 322, 330, 326, 333, 345, 338, 354]
            },
            {
              borderColor: "#f17e5d",
              backgroundColor: "#f17e5d",
              pointRadius: 0,
              pointHoverRadius: 0,
              borderWidth: 3,
              data: [320, 340, 365, 360, 370, 385, 390, 384, 408, 420]
            },
            {
              borderColor: "#fcc468",
              backgroundColor: "#fcc468",
              pointRadius: 0,
              pointHoverRadius: 0,
              borderWidth: 3,
              data: [370, 394, 415, 409, 425, 445, 460, 450, 478, 484]
            }
          ]
        },
        options: {
          legend: {
            display: false
          },

          tooltips: {
            enabled: false
          },

          scales: {
            yAxes: [{

              ticks: {
                fontColor: "#9f9f9f",
                beginAtZero: false,
                maxTicksLimit: 5,
                //padding: 20
              },
              gridLines: {
                drawBorder: false,
                zeroLineColor: "#ccc",
                color: 'rgba(255,255,255,0.05)'
              }

            }],

            xAxes: [{
              barPercentage: 1.6,
              gridLines: {
                drawBorder: false,
                color: 'rgba(255,255,255,0.1)',
                zeroLineColor: "transparent",
                display: false,
              },
              ticks: {
                padding: 20,
                fontColor: "#9f9f9f"
              }
            }]
          },
        }
      });


      this.canvas = document.getElementById("chartEmail");
      this.ctx = this.canvas.getContext("2d");
      this.chartEmail = new Chart(this.ctx, {
        type: 'pie',
        data: {
          labels: [1, 2, 3],
          datasets: [{
            label: "Emails",
            pointRadius: 0,
            pointHoverRadius: 0,
            backgroundColor: [
              '#e3e3e3',
              '#4acccd',
              '#fcc468',
              '#ef8157'
            ],
            borderWidth: 0,
            data: [342, 480, 530, 120]
          }]
        },

        options: {

          legend: {
            display: false
          },

          pieceLabel: {
            render: 'percentage',
            fontColor: ['white'],
            precision: 2
          },

          tooltips: {
            enabled: false
          },

          scales: {
            yAxes: [{

              ticks: {
                display: false
              },
              gridLines: {
                drawBorder: false,
                zeroLineColor: "transparent",
                color: 'rgba(255,255,255,0.05)'
              }

            }],

            xAxes: [{
              barPercentage: 1.6,
              gridLines: {
                drawBorder: false,
                color: 'rgba(255,255,255,0.1)',
                zeroLineColor: "transparent"
              },
              ticks: {
                display: false,
              }
            }]
          },
        }
      });

      var speedCanvas = document.getElementById("speedChart");

      var dataFirst = {
        data: [0, 19, 15, 20, 30, 40, 40, 50, 25, 30, 50, 70],
        fill: false,
        borderColor: '#fbc658',
        backgroundColor: 'transparent',
        pointBorderColor: '#fbc658',
        pointRadius: 4,
        pointHoverRadius: 4,
        pointBorderWidth: 8,
      };

      var dataSecond = {
        data: [0, 5, 10, 12, 20, 27, 30, 34, 42, 45, 55, 63],
        fill: false,
        borderColor: '#51CACF',
        backgroundColor: 'transparent',
        pointBorderColor: '#51CACF',
        pointRadius: 4,
        pointHoverRadius: 4,
        pointBorderWidth: 8
      };

      var speedData = {
        labels: ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"],
        datasets: [dataFirst, dataSecond]
      };

      var chartOptions = {
        legend: {
          display: false,
          position: 'top'
        }
      };

      var lineChart = new Chart(speedCanvas, {
        type: 'line',
        hover: false,
        data: speedData,
        options: chartOptions
      });

    this.usuarioService.listaUsuarios(this.usuarioGuardado.getToken()).subscribe( data => {
 //     console.log("lista usuarios");
 //     console.log(data.data);
    });
    }


    prepararEditar(id, operacion) {
//      console.log("prepararEditar: " + id + " " + operacion);
      this.router.navigateByUrl('/actividad/'+id+'/'+operacion);
    }

    
    prepararNuevaActividad() {
      this.router.navigateByUrl('nueva-actividad');
    }
}
